<template>
  <div class="container">
    <SecondTitle :title="secondTitle" @cancel="back"></SecondTitle>
    <div class="content">
      <div class="btnGroup">
        <button v-for="(btn, index) in buttonGroup" :key="index" :class="['btn', {'btnWeek': index === 0, 'btnMonth': index === 1, 'active': index === currentIndex}]" @click.stop="changeSelect(index)">{{btn}}</button>
      </div>
      <div class="datePicker">
        <div class="leftArrow">
          <i v-show="isLeftArrowShow" @click.stop="leftClick"></i>
        </div>
        <div class="dateText">{{dateText}}</div>
        <div class="rightArrow">
          <i v-show="isRightArrowShow" @click.stop="rightClick"></i>
        </div>
      </div>
      <div id="chart">

      </div>
      <div class="dataView">
        <div v-for="(item, index) in dataViewArr" :key="index" class="item">
          <div class="itemName">{{item.name}}</div>
          <div class="itemNumber">
            <div class="itemVal">{{item.value}}</div>
            <span class="itemWord">{{item.word}}</span>
          </div>
        </div>
      </div>
      <p class="errorTips" v-if="faultDetection.status">因设备故障，数据可能不准确</p>
    </div>
  </div>
</template>
<script>
/**
  统计数据格式暂未定，以下为个人模拟数据
**/
import SecondTitle from './SecondTitle'
import {mapState} from 'vuex'
import jsonData from '../mock/statistics'
// 按需引入echarts
let echarts = require('echarts/lib/echarts')
// 引入柱状图组件
require('echarts/lib/chart/bar')
// 引入提示框和title组件
require('echarts/lib/component/tooltip')
export default {
  components: {
    SecondTitle
  },
  created () {
    let week = []
    let weekDate = []
    let weekValue = []
    let month = []
    let monthDate = []
    let monthValue = []
    jsonData.weekData.forEach((item) => {
      let weeks = item.week.split('-')[0].split('/')[1] + '/' + item.week.split('-')[0].split('/')[2] + '-' +
                  item.week.split('-')[1].split('/')[1] + '/' + item.week.split('-')[1].split('/')[2]
      week.push(weeks)
      weekValue.push(item.value)
      weekDate.push(item.week)
    })
    jsonData.monthData.forEach((item) => {
      let months = item.month.split('/')[1]
      month.push(months)
      monthValue.push(item.value)
      monthDate.push(item.month)
    })
    this.weekData = [week, weekValue, weekDate]
    this.monthData = [month, monthValue, monthDate]
    if (week.length < 12) {
      this.isLeftArrowShow = 0
    } else {
      this.isLeftArrowShow = 1
    }
  },
  data () {
    return {
      secondTitle: '统计',
      currentIndex: 0,
      buttonGroup: ['周', '月'],
      weekData: [],
      monthData: [],
      yAxisData: [],
      xAxixData: [],
      dateText: '',
      isRightArrowShow: 0,
      isLeftArrowShow: 0
    }
  },
  mounted () {
    let len = this.weekData[2].length
    this.dateText = this.weekData[2][len - 12].split('-')[0] + '-' + this.weekData[2][len - 1].split('-')[1]
    this.xAxixData = this.weekData[0].slice(-12)
    this.yAxisData = this.weekData[1].slice(-12)
    this.drawChart()
  },
  methods: {
    // 返回
    back () {
      this.$router.go(-1)
    },
    // 周或月选择
    changeSelect (index) {
      this.currentIndex = index
      if (index === 0) {
        this.isLeftArrowShow = 1
        this.isRightArrowShow = 0
        let len = this.weekData[2].length
        this.dateText = this.weekData[2][len - 12].split('-')[0] + '-' + this.weekData[2][len - 1].split('-')[1]
        this.xAxixData = this.weekData[0].slice(-12)
        this.yAxisData = this.weekData[1].slice(-12)
      } else {
        this.isLeftArrowShow = 0
        this.isRightArrowShow = 0
        this.dateText = this.monthData[2][0] + '-' + this.monthData[2][this.monthData[2].length - 1]
        this.yAxisData = this.monthData[1]
        this.xAxixData = this.monthData[0]
      }
      this.drawChart()
    },
    // 左翻按钮
    leftClick () {
      setTimeout(() => {
        this.drawChart()
      }, 1000)
    },
    // 右翻按钮
    rightClick () {
      setTimeout(() => {
        this.drawChart()
      }, 1000)
    },
    // 绘制图表
    drawChart () {
      let chartBox = echarts.init(document.getElementById('chart'))
      // 清除画布
      chartBox.clear()
      // 配置选项
      chartBox.setOption({
        grid: {
          top: 115,
          left: 25,
          right: 25
        },
        tooltip: {
          backgroundColor: 'rgba(85, 85, 85, 0.8)',
          padding: [8, 15],
          extraCssText: 'text-align: left; border-radius: 10px;',
          triggerOn: 'click',
          position: (pos, params, dom, rect, size) => {
            let arrowDown = document.getElementsByClassName('arrowDown')
            let obj = {bottom: rect.height + 75}
            let objLeft = pos[0] - size.contentSize[0] / 2
            // 调整箭头位置
            for (let i = 0; i < arrowDown.length; i++) {
              if (objLeft < 0) {
                arrowDown[i].style.left = pos[0] - 14 + 'px'
              } else if (objLeft > size.viewSize[0] - size.contentSize[0]) {
                arrowDown[i].style.right = size.viewSize[0] - pos[0] - 12 + 'px'
              } else {
                arrowDown[i].style.left = size.contentSize[0] / 2 - 10 + 'px'
              }
            }
            // 调整提示框位置
            obj['left'] = objLeft < 0 ? 5 : (objLeft > size.viewSize[0] - size.contentSize[0] ? size.viewSize[0] - size.contentSize[0] : objLeft)
            return obj
          },
          formatter: (params) => {
            return params.name + '<br>' +
            '清扫面积：' + params.value + 'm²<br>' +
            '慢跑里程：' + params.value + 'm<br>' +
            '卡路里消耗：' + params.value + 'kcal<br>' +
            '<p class="arrowDown" style="position:absolute; bottom: -10px; width: 0; height: 0; border-right: 10px solid transparent; border-left: 10px solid transparent;border-top: 10px solid rgba(85, 85, 85, 0.8);"></p>'
          }
        },
        xAxis: {
          axisLine: {
            lineStyle: {
              color: 'rgba(0, 0, 0, 0.3)'
            }
          },
          axisTick: {
            show: false
          },
          axisLabel: {
            color: 'rgba(0, 0, 0, 0.5)',
            fontWeight: 500
          },
          data: this.xAxixData
        },
        yAxis: {
          show: false
        },
        series: [{
          type: 'bar',
          barMaxWidth: 10,
          data: this.yAxisData,
          itemStyle: {
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
              offset: 0,
              color: '#66BAFF'
            }, {
              offset: 1,
              color: '#AF81D7'
            }]),
            barBorderRadius: [5, 5, 0, 0]
          }
        }]
      })
      // 自动展示最后一列的数据
      setTimeout(() => {
        chartBox.dispatchAction({
          type: 'showTip',
          seriesIndex: 0,
          dataIndex: 11,
          position: (pos, params, dom, rect, size) => {
            let arrowDown = document.getElementsByClassName('arrowDown')
            // 调整箭头位置
            for (let i = 0; i < arrowDown.length; i++) {
              arrowDown[i].style.right = size.viewSize[0] - pos[0] - 15 + 'px'
            }
            let obj = {bottom: rect.height + 80, right: 5}
            return obj
          }
        })
      }, 500)
    }
  },
  computed: {
    ...mapState(['cleanerRobot', 'faultDetection']),
    // 数据展示模块数据
    dataViewArr () {
      return [
        {
          name: '累计清扫面积',
          value: this.cleanerRobot.totalarea,
          word: 'm²'
        },
        {
          name: '累计卡路里消耗',
          value: this.cleanerRobot.totalconsume,
          word: 'kcal'
        },
        {
          name: '累计慢跑路程',
          value: this.cleanerRobot.totaldistance,
          word: 'm'
        }
      ]
    }
  }
}
</script>
<style scoped>
  .container{
    width: 100%;
    height: 100%;
    background: #ffffff;
    display: flex;
    flex-direction: column;
  }
  .container::before{
    display: table;
    content: '';
  }
  .content{
    flex: 1;
    padding: 1.2rem;
    flex-direction: column;
    justify-content: center;
  }
  /* 按钮切换样式 */
  .btnGroup{
    text-align: center;
    height: 4rem;
    line-height: 4rem;
  }
  .btn{
    width: 10rem;
    height: 3rem;
    line-height: 3rem;
    border: 1px solid rgba(0, 125, 255, 0.15);
    background: #ffffff;
    color: rgba(0, 0, 0, 0.75);
    font-weight: 600;
    outline: none;
  }
  .btn.active{
    background: rgba(0, 125, 255, 0.15);
    color: #007DFF;
  }
  .btnWeek{
    border-right: none;
    border-radius: 1.5rem 0 0 1.5rem;
  }
  .btnMonth{
    border-radius: 0 1.5rem 1.5rem 0;
  }
  /* 日期选择样式 */
  .datePicker{
    height: 4.5rem;
    line-height: 4.5rem;
    display: flex;
  }
  .dateText{
    text-align: center;
    flex: 1;
    font-size: 1.3rem;
    opacity: 0.8;
  }
  .leftArrow, .rightArrow{
    width: 8rem;
    text-align: right;
  }
  .rightArrow{
    text-align: left;
  }
  .leftArrow i, .rightArrow i{
    margin-top: 1.1rem;
    display: inline-block;
    width: 1.2rem;
    height: 2.4rem;
    background-size: 1.2rem 2.4rem;
    background-image: url(../../static/img/icon_statistics_left.png);
    background-position: center;
    background-repeat: no-repeat;
  }
  .rightArrow i{
    background-image: url(../../static/img/icon_statistics_right.png);
  }
  /* 图表样式 */
  #chart{
    height: 50%;
    text-align: center;
    border-top: 1px solid rgba(226, 226, 226, 0.8);
  }
  /* 数据展示样式 */
  .dataView{
    height: 8rem;
    border-top: 1px solid rgba(226, 226, 226, 0.8);
    display: flex;
    padding: 0 2rem;
  }
  .item{
    flex: 1;
    display: flex;
    margin: 0 0.4rem;
    flex-direction: column;
    justify-content: center;
  }
  .itemName{
    font-size: 1.1rem;
    opacity: 0.5;
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .itemNumber{
    flex: 1;
    text-align: center;
  }
  .itemVal{
    font-size: 2.2rem;
    display: inline-block;
  }
  .itemWord{
    display: inline-block;
    font-size: 1.2rem;
    opacity: 0.5;
  }
  /* 故障提示 */
  .errorTips{
    opacity: 0.5;
    font-size: 1.2rem;
    text-align: center;
    margin-top: 1rem;
  }
</style>



